import _ from 'lodash';
import React, {PureComponent} from 'react';
import classNames from 'classnames';
import * as PropTypes from 'prop-types';
import {classPrefix} from 'variables';

class TextArea extends PureComponent {

  render() {
    const {defaultValue, isDisabled, isReadOnly, id, value, name, placeholder} = this.props;
    const {size, textAreaStyle} = this.props;
    const {onChange, onBlur, onClick} = this.props;
    const {className, attributes} = this.props;

    /**  */
    const classes = classNames(
      `${classPrefix}-form-control`,
      {
        [`form-control-${size}`]: _.includes(['small', 'large'], size),
        [`form-control--${textAreaStyle}`]: _.includes(['pill', 'square'], textAreaStyle),
      },
      className,
    );

    // 
    const callbacks = {onChange, onBlur, onClick};

    // 
    const props = {
      defaultValue,
      value,
      placeholder,
      name,
      id,
      disabled: isDisabled,
      readOnly: isReadOnly,
      className: classes,
      ...callbacks,
      ...attributes,
    };


    return <textarea {...props} />;
  }
}

TextArea.propTypes = {
  /**  */
  defaultValue: PropTypes.node,
  /**  */
  value: PropTypes.node,
  /**  */
  name: PropTypes.node,
  /**  */
  placeholder: PropTypes.node,
  /** ID */
  id: PropTypes.node,
  /**  */
  isDisabled: PropTypes.bool,
  /**  */
  isReadOnly: PropTypes.bool,
  /**  default / small / large */
  size: PropTypes.oneOf(['default', 'small', 'large']),
  /** default / pill / square */
  textAreaStyle: PropTypes.oneOf(['default', 'pill', 'square']),
  /**  */
  onChange: PropTypes.func,
  /**  */
  onBlur: PropTypes.func,
  /**  */
  onClick: PropTypes.func,
  /**  */
  className: PropTypes.string,
  /**  */
  attributes: PropTypes.object,
};

TextArea.defaultProps = {
  defaultValue: undefined,
  value: undefined,
  placeholder: undefined,
  name: undefined,
  id: undefined,
  isDisabled: false,
  isReadOnly: false,
  size: 'default',
  textAreaStyle: 'default',
  onChange: undefined,
  onBlur: undefined,
  onClick: undefined,
  className: '',
  attributes: {},
};

export default TextArea;
