import React, {PureComponent} from 'react';
import _ from 'lodash';
import classNames from 'classnames';
import * as PropTypes from 'prop-types';
import {classPrefix} from 'variables';
import './style/index.scss';

/**  */
const TYPES = ['flex'];
/**  */
const ALIGN = ['top', 'middle', 'bottom'];
/**  */
const JUSTIFY = ['start', 'end', 'center', 'space-around', 'space-between'];

class Row extends PureComponent {

  render() {
    const {
      justify,
      align,
      className,
      attributes,
      children,
    } = this.props;

    /**  */
    const classes = classNames(
      `${classPrefix}-row`,
      `${classPrefix}-row--root`,
      {
        [`${classPrefix}-row--justify-${justify}`]: !_.isUndefined(justify),
        [`${classPrefix}-row--align-${align}`]: !_.isUndefined(align),
      },
      className,
    );

    return (
      <div
        className={classes}
        {...attributes}
      >
        {children}
      </div>
    );
  }
}

Row.propTypes = {
  /**  flex */
  type: PropTypes.oneOf(TYPES),
  /** 'start', 'end', 'center', 'space-around', 'space-between' */
  justify: PropTypes.oneOf(JUSTIFY),
  /** 'top', 'middle', 'bottom' */
  align: PropTypes.oneOf(ALIGN),
  /**  */
  className: PropTypes.string,
  /**  */
  attributes: PropTypes.object,
};

Row.defaultProps = {
  type: 'flex',
  justify: 'start',
  align: 'top',
  className: '',
  attributes: {},
};

export default Row;


