import React, {PureComponent} from 'react';
import _ from 'lodash';
import classNames from 'classnames';
import * as PropTypes from 'prop-types';
import {classPrefix} from 'variables';
import './style/index.scss';

class Col extends PureComponent {
  constructor(props) {
    super(props);

    this.onColClasses = this.onColClasses.bind(this);
  }

  /**
   *  param  param-xxx  col-3,offset-3order-3
   *  param  param-key-value  col-md-3,offset-md-3,order-md-3
   *  key  default  param-xxx  col-3,offset-3order-3
   * @return Array Col 
   */
  onColClasses(param) {
    const data = this.props[param];

    if (_.isNumber(data) || _.isString(data)) {
      return [`${classPrefix}-${param}-${data}`];
    }

    const colClasses = [];
    if (_.isObject(data)) {
      _.forIn(data, (value, key) => {
        colClasses.push(_.isEqual(key, 'default') ? `${classPrefix}-${param}-${value}` : `${classPrefix}-${param}-${key}-${value}`);
      });
    }

    return colClasses;
  }


  render() {
    const {className, attributes, children} = this.props;
    const special = ['col', 'offset', 'order'];
    /**  */
    const classes = classNames(
      _.flatten(_.map(special, item => this.onColClasses(item))),
      className,
    );
    return (
      <div
        className={classes}
        {...attributes}
      >
        {children}
      </div>
    );
  }
}

Col.propTypes = {
  /**  */
  col: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
    PropTypes.object,
  ]),
  /**  */
  offset: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.object,
  ]),
  /**  */
  order: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.object,
  ]),
  /**  */
  className: PropTypes.string,
  /**  */
  attributes: PropTypes.object,
};

Col.defaultProps = {
  col: 'auto',
  offset: undefined,
  order: undefined,
  className: '',
  attributes: {},
};

export default Col;
